.promo-trigger {
    position: fixed !important;
    right: 25px;
    bottom: 0;
    display: flex;
    align-items: center;
    transition: all .4s;
    border-radius: 30px;
    transform: translateY(100%);
    box-shadow: 0 5px 7px 0 rgba(0, 0, 0, .02);
    color: #fff !important;
    background: #2b56f5 !important;
    z-index: 999999
}

.promo-trigger:hover, .promo-trigger:focus, .promo-trigger:active {
    color: #fff !important
}

.promo-trigger.active {
    bottom: 25px;
    transform: translateY(0);
    transition-delay: .4s
}

.promo-trigger-img {
    width: 60px;
    height: 60px;
    border-radius: 30px;
    padding: 14px;
    transition: all .4s
}

.promo-trigger-text {
    padding: 14px 0;
    height: 60px;
    width: 0;
    overflow: hidden;
    white-space: nowrap;
    text-transform: uppercase;
    letter-spacing: .1em;
    font-weight: 600;
    transition: all .4s;
    visibility: hidden;
    font-size: 12px;
    line-height: 16px;
    color: #fff !important
}

.promo-trigger:hover .promo-trigger-text {
    width: 200px;
    visibility: visible;
    padding: 14px 14px 14px 0
}

.promo-content {
    position: fixed !important;
    right: 30px;
    bottom: -50px;
    width: 380px;
    transform: translateY(100%);
    transition: all .4s;
    background: #fff !important;
    border-radius: 6px;
    box-shadow: 0 6px 60px 0 rgba(18, 26, 38, .15);
    z-index: 999999
}

.promo-content.active {
    transform: translateY(0);
    bottom: 30px;
    transition-delay: 4s
}

.promo-content-wrap {
    display: flex;
    padding: 20px;
    align-items: center
}

.promo-content-img {
    width: 84px;
    flex-shrink: 0;
    margin-right: 15px;
    border: 2px solid #2b56f5 !important
}

.has-rtl .promo-content-img {
    margin-right: 0;
    margin-left: 15px
}

.promo-content-img img {
    vertical-align: middle
}

.promo-content-text h5 {
    font-size: 12px;
    line-height: 22px;
    color: #202737 !important;
    font-weight: 500;
    margin-bottom: 7px
}

.promo-content-text h5 span {
    font-size: 14px;
    color: #2b56f5 !important
}

.promo-content-text p {
    font-size: 11px;
    line-height: 18px;
    color: #737e97 !important;
    font-weight: 500
}

.promo-close {
    color: #fff !important;
    border-radius: 50%;
    height: 36px;
    width: 36px;
    line-height: 38px;
    background: #596780 !important;
    text-align: center;
    display: inline-block;
    position: absolute;
    top: -18px;
    right: -18px;
    font-size: 12px;
    box-shadow: 0 0 15px 0 rgba(18, 26, 38, .55)
}

.promo-close:hover {
    color: #fff !important;
    background: #2b56f5 !important
}

