    }
    60% {
        transform: translateY(6px)
    }
}

@keyframes upDown3 {
    0%, 100%, 20%, 50%, 80% {
        transform: translateY(0)
    }
    40% {
        transform: translateY(3px)
    }
    60% {
        transform: translateY(4px)
    }
}

