  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  text-align: center;
}

.score-text {
  margin: auto;
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  height: 1em;
  line-height: 1em;
  font-size: 32px;
}

#live-mint-feed {
  white-space: nowrap;
  overflow-x: scroll;
  -webkit-overflow-scrolling: touch;
  height: 300px;
}

#live-mint-feed .slide {
  width: 200px;
  display: inline-block;
  text-align: center;
  margin-right: 15px;
}

#live-mint-feed .slide .image {
  display: block;
  width: 100%;
  display: block;
  border-radius: 5px;
  overflow: hidden;
  margin-block: 15px;
  position: relative;
  box-shadow: 0 3px 8px 0 rgba(0, 0, 0, 0.15);
}

#live-mint-feed .slide .image:after {
  content: "";
  display: block;
  padding-top: 100%;
}

